<template>
    <div>
        <div class="layui-card">
            <div class="layui-card-header"></div>
            <div class="layui-card-body">
                <!--<button type="button" class="layui-btn layui-btn-primary" @click="haha"></button>-->
                <table class="layui-hide" id="demo" lay-filter="demo"></table>
            </div>
        </div>
    </div>
</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    /* eslint no-undef: "off"*/
    /* eslint no-unused-vars: "off"*/
    import '@/assets/js/include.js'
    export default {
        name: "index",
        data(){
            return{
                news: null
            }
        },
        methods: {
            updata:function () {
                // this.test = [{
                //     "id": "10001"
                //     ,"username": ""
                //     ,"email": "xianxin@layui.com"
                //     ,"sex": ""
                //     ,"city": ""
                //     ,"sign": ""
                //     ,"experience": "116"
                //     ,"ip": "192.168.0.8"
                //     ,"logins": "108"
                //     ,"joinTime": "2016-10-14"
                // }];
                var _this = this
                layui.use('table', function(){
                    var table = layui.table;

                    //
                    table.render({
                        elem: '#demo'
                        ,defaultToolbar: ['filter', 'exports', 'print', { //
                            title: ''
                            ,layEvent: 'LAYTABLE_TIPS'
                            ,icon: 'layui-icon-tips'
                        }]
                        ,cols: [[ //
                            {type:'checkbox',fixed:"left"}
                            ,{field: 'id', title: 'ID', width: 80, sort: true,fixed: 'left', unresize: true}
                            ,{field: 'title', title: '', width: 120}
                            ,{field: 'content', title: '', minWidth: 150}
                            ,{field: 'date', title: '', minWidth: 160}
                            ,{field: 'used', title: '', width: 80}
                            ,{field: 'user', title: '', width: 100}
                            ,{fixed: 'right', title:'', toolbar: '#barDemo', width:150}
                        ]]
                        ,data: _this.test
                        //,skin: 'line' //
                        ,even: true
                        ,page: true //
                        //,limits: [5, 7, 10]
                        //,limit: 5 //
                    });
                    table.on('toolbar(demo)', function(obj){
                        var checkStatus = table.checkStatus(obj.config.id);
                        var data;
                        switch(obj.event){

                            case 'getCheckData':
                                data = checkStatus.data;
                                layer.alert(JSON.stringify(data));
                                break;
                            case 'getCheckLength':
                                data = checkStatus.data;
                                layer.msg(''+ data.length + ' ');
                                break;
                            case 'isAll':
                                layer.msg(checkStatus.isAll ? '': '');
                                break;

                            // - 
                            case 'LAYTABLE_TIPS':
                                layer.alert('');
                                break;
                        }
                    });

                    //
                    table.on('tool(demo)', function(obj){
                        var data = obj.data;
                        console.log(obj)
                        if(obj.event === 'del'){
                            layer.confirm('', function(index){
                                obj.del();
                                layer.close(index);
                            });
                        } else if(obj.event === 'edit'){
                            layer.prompt({
                                formType: 2
                                ,value: data.email
                            }, function(value, index){
                                obj.update({
                                    email: value
                                });
                                layer.close(index);
                            });
                        }
                    });
                });
            }
        },
        created(){
            this.haha();
        }
    }
</script>

